import { useContext } from 'react'
import { DataContext } from '../MyContext'
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMinus } from '@fortawesome/free-solid-svg-icons'

export default function ToDoList() {
  const [data, setData, saveData] = useContext(DataContext)

  const deleteItem = (id) => {
    const filteredData = data.filter((item) => {
      return item.id !== id
    })
    setData(filteredData)
    saveData(filteredData)
  }

  const handleEnd = (result) => {
    // console.log(result);
    if (!result.destination) return
    const items = Array.from(data)
    const [reorderedItem] = items.splice(result.source.index, 1)
    items.splice(result.destination.index, 0, reorderedItem)
    setData(items)
    saveData(items)
  }

  const getItemStyle = (isDragging, draggableStyle) => ({
    userSelect: 'none',
    background: isDragging ? '#ABABAB' : 'transparent',
    // styles we need to apply on draggables
    ...draggableStyle,
  })

  return (
    <div className="m-auto px-4 col-12 col-sm-10 col-lg-6">
      <DragDropContext onDragEnd={handleEnd}>
        <Droppable droppableId="to-dos">
          {(provided) => (
            <ul
              className="list-group"
              {...provided.droppableProps}
              ref={provided.innerRef}
            >
              {data.map((item, index) => (
                <Draggable
                  key={item.id}
                  draggableId={item.id.toString()}
                  index={index}
                >
                  {(provided, snapshot) => (
                    <li
                      className="border d-flex justify-content-between align-items-center p-2 m-2"
                      {...provided.draggableProps}
                      ref={provided.innerRef}
                      {...provided.dragHandleProps}
                      key={item.id}
                      style={getItemStyle(
                        snapshot.isDragging,
                        provided.draggableProps.style
                      )}
                    >
                      <div className="p-2 text-white item-txt">
                        {index + 1}. {item.txt}
                      </div>

                      <button
                        onClick={() => deleteItem(item.id)}
                        className="btn btn-danger p-2"
                      >
                        <FontAwesomeIcon icon={faMinus} />
                      </button>
                    </li>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
            </ul>
          )}
        </Droppable>
      </DragDropContext>
    </div>
  )
}
