import { useContext, useState } from 'react'
import { DataContext } from '../MyContext'
import { nanoid } from 'nanoid'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus } from '@fortawesome/free-solid-svg-icons'

export default function Form() {
  const [data, setData, saveData] = useContext(DataContext)
  const [stateInput, setStateInput] = useState('')

  const linkedInput = (e) => {
    setStateInput(e)
  }

  const handleOnSubmit = (e) => {
    e.preventDefault()
    // const id = nanoid()
    const newTodos = [...data]
    const newItemTodolist = {}
    newItemTodolist.id = nanoid()
    newItemTodolist.txt = stateInput

    newTodos.push(newItemTodolist)
    setData(newTodos)
    setStateInput('')
    saveData(newTodos)
  }

  return (
    <div className="m-auto px-4 col-12 col-sm-10 col-lg-6">
      <form onSubmit={(e) => handleOnSubmit(e)} className="mb-3">
        <label htmlFor="inp-todo" className="form-label mt-3"></label>
        <input
          type="text"
          className="form-control inp-todo"
          style={{background: "#D4D4D4"}}
          id="inp-todo"
          value={stateInput}
          onChange={(e) => linkedInput(e.target.value)}
        />
        <button className="mt-2 btn btn-outline-info d-block btn-add">
          <FontAwesomeIcon icon={faPlus} /> 
        </button>
      </form>
    </div>
  )
}
